.textarea {
    min-height: 200px;
    width: 100%;
    padding: 7px 20px;
    border: 1px solid #f2e9e0;
    border-radius: 2px;
    font-family: inherit;
    background-color: #f2e9e0;
    transition: all .3s;

    &:focus {
        box-shadow: inset 0 0 0 1px rgba(0, 0, 0, .1);
        outline: 0;
        background-color: #fff;
    }
}
